    /**** #.overlay ****/
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
    display: none;
    z-index: 10;
    justify-content: center;
    align-items: center;
  }
  
  /**** #.confirm-modal ****/
  .confirm-modal {
    max-width: 30rem;
    width: 100%;
    min-height: 25rem;
    padding-top: 2rem;
    background-color: #fff;
    -webkit-animation: fadeInDown 1s ease;
            animation: fadeInDown 1s ease;
    box-shadow: 0 0 7rem 0 #000;
  }
  
  /**** #.confirm-title ****/
  .confirm-title {
    font-size: 2rem;
    text-align: center;
    margin-bottom: 1rem;
    margin-top: -10px;
  }
  
  /**** #.confirm-buttons ****/
  .confirm-buttons {
    display: flex;
    justify-content: center;
    max-width: 25rem;
    width: 100%;
    margin: auto;
  }
  
  /**** #.confirm-btn ****/
  .confirm-btn {
    display: inline-block;
    padding: 1rem;
    border: none;
    font-family: inherit;
    font-size: 1.5rem;
    font-weight: 400;
    transition: all 0.3s ease-in-out;
    color: #ffffff;
    cursor: pointer;
    width: 48%;
  }
  
  .confirm-btn:hover {
    box-shadow: 0 1rem 5rem rgba(0, 0, 0, 0.6);
  }
  
  /**** #.yes-btn ****/
  .yes-btn {
    background-color: #e74c3c;
  }

  img{
      width: 50%;
      height: 50%;
      margin-bottom: 1rem;
  }
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #8f8f8f;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

